var React = require('react');
var ReactDOM = require('react-dom');
var bootstrap = require('react-bootstrap');

var PeopleBox = React.createClass({
	render: function(){
		return (
			<div>
				<PeopleHeader></PeopleHeader>
				<PeopleForm></PeopleForm>
			</div>
		);
	}
});

var PeopleHeader = React.createClass({
	render: function(){
		return (
			<bootstrap.Navbar toggleNavKey={0}>
				<bootstrap.CollapsibleNav eventKey={0}>
					<bootstrap.Nav navbar left>
						<bootstrap.NavItem href="/contacts" eventKey={1}>取消</bootstrap.NavItem>
					</bootstrap.Nav>
					<bootstrap.Nav navbar right>
						<bootstrap.NavItem eventKey={2}>确定</bootstrap.NavItem>
					</bootstrap.Nav>
				</bootstrap.CollapsibleNav>
			</bootstrap.Navbar> 
		);
	}
});

var PeopleForm = React.createClass({
	render: function(){
		return (
			<form></form>
		);
	}
});

ReactDOM.render(<PeopleBox />, getElementById('content'));


